.modal__btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: fill var(--time) var(--cubic);
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: inline-flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__btn:hover, .modal__btn:focus {
